.right_add {
  display: inline-block;
  position: relative;
  width: 100%;
  margin-bottom: 145px;
}
.add_new {
  width: 24px;
  height: 24px;
  display: block;
  position: absolute;
  top: 50%;
  right: 5%;
  transform: translate(0, -50%);
  cursor: pointer;
}
.add_new::before,
.add_new::after {
  position: absolute;
  content: '';
  background: #ff4384;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.add_new::before {
  width: 2px;
  height: 15px;
}
.add_new::after {
  width: 15px;
  height: 2px;
}
input[type='text'] {
  font-family: 'Roboto', sans-serif, '微軟正黑體';
  font-size: 16px;
  color: #ff4384;
  font-weight: bold;
  padding: 19px 60px 19px 16px;
  margin: 0;
  border: none;
  width: 100%;
  box-sizing: border-box;
}
