.Right_list {
  margin-top: 107px;
}
.list_item {
  font-size: 16px;
  color: #003164;
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  transition: 0.3s;
  border-bottom: 2px solid #ccc;
  margin-bottom: 9px;
  span {
    position: relative;
    padding-left: 24px;
    display: inline-block;
    vertical-align: middle;
    line-height: 30px;
    &::before {
      position: absolute;
      content: '';
      width: 18px;
      height: 18px;
      box-sizing: border-box;
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
      border: 2px solid #003164;
      border-radius: 50%;
      transition: 0.3s;
    }
  }
  &:hover {
    color: #ff4384;
    span::before {
      border: 2px solid #ff4384;
    }
  }
}
.more {
  text-align: right;
  a {
    color: #ff4384;
    font-weight: bold;
    cursor: pointer;
    display: inline-block;
  }
}
.on {
  color: #ff4384;
  span::before {
    border: 2px solid #ff4384;
  }
}
