.player {
  position: absolute;
  right: -270px;
  top: 130px;
  background: transparent;
  box-sizing: border-box;
  border-radius: 50%;
  width: 540px;
  height: 540px;
  z-index: 10;
  padding: 15px;
  border: 4px solid #ff4384;
}
.player_wrap {
  background: #ff4384;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
canvas {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.play_btn {
  position: absolute;
  width: 96px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
}
.play_btn::after {
  display: inline-block;
  content: '';
  width: 10px;
  height: 10px;
  background: #fff;
  position: absolute;
  bottom: 0;
  right: 0;
}
.play_btn i {
  font-size: 96px;
  color: #fff;
  user-select: none;
}
.on {
  .player_wrap {
    background: #fff;
    border: 4px solid #ff4384;
    box-sizing: border-box;
  }
  .play_btn i {
    color: #ff4384;
  }
  .play_btn::after {
    background: #ff4384;
  }
}
