.app {
  width: 100%;
  display: flex;
  min-width: 1280px;
  min-height: 800px;
  box-sizing: border-box;
  position: relative;
}
.right,
.left {
  box-sizing: border-box;
  padding: 48px 85px;
  min-height: 100vh;
  height: auto;
}
.right {
  background: #ffedf7;
  width: 100%;
  position: relative;
}
.left {
  min-width: 450px;
  background: #003164;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  ul {
    li + li {
      margin-top: 48px;
    }
    i {
      font-size: 36px;
      cursor: pointer;
      -webkit-user-select: none;
      user-select: none;
    }
  }
  span {
    -webkit-writing-mode: vertical-rl;
    writing-mode: vertical-rl;
    font-size: 30px;
    letter-spacing: 2px;
    font-weight: bold;
  }
}
.right_wrap {
  width: 445px;
}
