.title {
  font-size: 24px;
  font-weight: bold;
  color: #003164;
  position: relative;
  padding-left: 66px;
}
.title::before {
  display: block;
  content: '';
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 2px solid #003164;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
}
.circle {
  margin-top: 8px;
}
.circle span {
  display: block;
  box-sizing: border-box;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 1px solid #ff4384;
}
.time {
  font-size: 176px;
  color: #ff4384;
  font-weight: bold;
  padding: 15px 0;
}
